'use client'

import { useState, useEffect } from 'react'
import AdminLayout from '@/components/AdminLayout'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Switch } from '@/components/ui/switch'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface SystemSettings {
  orderCutoffTime: string
  enableOrderNotifications: boolean
  enableEmailNotifications: boolean
  enableSMSNotifications: boolean
  defaultCurrency: string
  companyName: string
  companyAddress: string
  companyPhone: string
  companyEmail: string
  orderReminderTime: string
  maxOrdersPerDay: number
  enableWeekendOrders: boolean
  autoLockMenuTime: string
  defaultMenuNotes: string
}

export default function SettingsPage() {
  const [settings, setSettings] = useState<SystemSettings>({
    orderCutoffTime: '11:00',
    enableOrderNotifications: true,
    enableEmailNotifications: false,
    enableSMSNotifications: false,
    defaultCurrency: 'BDT',
    companyName: 'Your Company Name',
    companyAddress: 'Company Address',
    companyPhone: '+880-1234567890',
    companyEmail: 'admin@company.com',
    orderReminderTime: '10:30',
    maxOrdersPerDay: 1,
    enableWeekendOrders: false,
    autoLockMenuTime: '12:00',
    defaultMenuNotes: 'Today\'s lunch menu'
  })
  const [isLoading, setIsLoading] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  useEffect(() => {
    loadSettings()
  }, [])

  const loadSettings = async () => {
    try {
      setIsLoading(true)
      setError('')

      const response = await fetch('/api/settings')
      if (!response.ok) {
        throw new Error('Failed to load settings')
      }
      const data = await response.json()
      setSettings(data.settings)
    } catch (error) {
      console.error('Error loading settings:', error)
      setError('Failed to load settings')
    } finally {
      setIsLoading(false)
    }
  }

  const handleSaveSettings = async () => {
    try {
      setIsSaving(true)
      setError('')

      if (!settings.orderCutoffTime) {
        setError('Order cutoff time is required')
        return
      }

      if (!settings.companyName.trim()) {
        setError('Company name is required')
        return
      }

      const response = await fetch('/api/settings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(settings)
      })

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || 'Failed to save settings')
      }

      setSuccess('Settings saved successfully!')
      setTimeout(() => setSuccess(''), 3000)
    } catch (error) {
      console.error('Error saving settings:', error)
      setError(error instanceof Error ? error.message : 'Failed to save settings')
    } finally {
      setIsSaving(false)
    }
  }

  const handleInputChange = (field: keyof SystemSettings, value: any) => {
    setSettings(prev => ({ ...prev, [field]: value }))
  }

  const handleResetToDefaults = () => {
    if (confirm('Are you sure you want to reset all settings to default values?')) {
      setSettings({
        orderCutoffTime: '11:00',
        enableOrderNotifications: true,
        enableEmailNotifications: false,
        enableSMSNotifications: false,
        defaultCurrency: 'BDT',
        companyName: 'Your Company Name',
        companyAddress: 'Company Address',
        companyPhone: '+880-1234567890',
        companyEmail: 'admin@company.com',
        orderReminderTime: '10:30',
        maxOrdersPerDay: 1,
        enableWeekendOrders: false,
        autoLockMenuTime: '12:00',
        defaultMenuNotes: 'Today\'s lunch menu'
      })
      setSuccess('Settings reset to defaults!')
      setTimeout(() => setSuccess(''), 3000)
    }
  }

  if (isLoading) {
    return (
      <AdminLayout>
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto mb-4"></div>
            <p>Loading settings...</p>
          </div>
        </div>
      </AdminLayout>
    )
  }

  return (
    <AdminLayout>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">System Settings</h1>
            <p className="text-gray-600 mt-2">
              Configure lunch management system preferences
            </p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" onClick={handleResetToDefaults}>
              Reset to Defaults
            </Button>
            <Button onClick={handleSaveSettings} disabled={isSaving}>
              {isSaving ? 'Saving...' : 'Save Settings'}
            </Button>
          </div>
        </div>

        {/* Alerts */}
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="border-green-200 bg-green-50">
            <AlertDescription className="text-green-800">{success}</AlertDescription>
          </Alert>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Order Settings */}
          <Card>
            <CardHeader>
              <CardTitle>Order Settings</CardTitle>
              <CardDescription>
                Configure order timing and restrictions
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="cutoffTime">Order Cutoff Time</Label>
                <Input
                  id="cutoffTime"
                  type="time"
                  value={settings.orderCutoffTime}
                  onChange={(e) => handleInputChange('orderCutoffTime', e.target.value)}
                />
                <p className="text-xs text-gray-500">
                  Orders will not be accepted after this time
                </p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="reminderTime">Order Reminder Time</Label>
                <Input
                  id="reminderTime"
                  type="time"
                  value={settings.orderReminderTime}
                  onChange={(e) => handleInputChange('orderReminderTime', e.target.value)}
                />
                <p className="text-xs text-gray-500">
                  Send reminder notifications at this time
                </p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="autoLockTime">Auto Lock Menu Time</Label>
                <Input
                  id="autoLockTime"
                  type="time"
                  value={settings.autoLockMenuTime}
                  onChange={(e) => handleInputChange('autoLockMenuTime', e.target.value)}
                />
                <p className="text-xs text-gray-500">
                  Automatically lock daily menu at this time
                </p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="maxOrders">Max Orders Per Employee Per Day</Label>
                <Input
                  id="maxOrders"
                  type="number"
                  min="1"
                  max="5"
                  value={settings.maxOrdersPerDay}
                  onChange={(e) => handleInputChange('maxOrdersPerDay', parseInt(e.target.value))}
                />
              </div>

              <div className="flex items-center space-x-2">
                <Switch
                  id="weekendOrders"
                  checked={settings.enableWeekendOrders}
                  onCheckedChange={(checked) => handleInputChange('enableWeekendOrders', checked)}
                />
                <Label htmlFor="weekendOrders">Enable Weekend Orders</Label>
              </div>
            </CardContent>
          </Card>

          {/* Notification Settings */}
          <Card>
            <CardHeader>
              <CardTitle>Notification Settings</CardTitle>
              <CardDescription>
                Configure notification preferences
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center space-x-2">
                <Switch
                  id="orderNotifications"
                  checked={settings.enableOrderNotifications}
                  onCheckedChange={(checked) => handleInputChange('enableOrderNotifications', checked)}
                />
                <Label htmlFor="orderNotifications">Enable Order Notifications</Label>
              </div>

              <div className="flex items-center space-x-2">
                <Switch
                  id="emailNotifications"
                  checked={settings.enableEmailNotifications}
                  onCheckedChange={(checked) => handleInputChange('enableEmailNotifications', checked)}
                />
                <Label htmlFor="emailNotifications">Enable Email Notifications</Label>
              </div>

              <div className="flex items-center space-x-2">
                <Switch
                  id="smsNotifications"
                  checked={settings.enableSMSNotifications}
                  onCheckedChange={(checked) => handleInputChange('enableSMSNotifications', checked)}
                />
                <Label htmlFor="smsNotifications">Enable SMS Notifications</Label>
              </div>

              <div className="space-y-2">
                <Label htmlFor="currency">Default Currency</Label>
                <Select 
                  value={settings.defaultCurrency} 
                  onValueChange={(value) => handleInputChange('defaultCurrency', value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select currency" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="BDT">BDT (৳)</SelectItem>
                    <SelectItem value="USD">USD ($)</SelectItem>
                    <SelectItem value="EUR">EUR (€)</SelectItem>
                    <SelectItem value="GBP">GBP (£)</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </CardContent>
          </Card>

          {/* Company Information */}
          <Card>
            <CardHeader>
              <CardTitle>Company Information</CardTitle>
              <CardDescription>
                Update company details for reports and receipts
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="companyName">Company Name</Label>
                <Input
                  id="companyName"
                  value={settings.companyName}
                  onChange={(e) => handleInputChange('companyName', e.target.value)}
                  placeholder="Enter company name"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="companyAddress">Company Address</Label>
                <Textarea
                  id="companyAddress"
                  value={settings.companyAddress}
                  onChange={(e) => handleInputChange('companyAddress', e.target.value)}
                  placeholder="Enter company address"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="companyPhone">Company Phone</Label>
                <Input
                  id="companyPhone"
                  value={settings.companyPhone}
                  onChange={(e) => handleInputChange('companyPhone', e.target.value)}
                  placeholder="Enter company phone"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="companyEmail">Company Email</Label>
                <Input
                  id="companyEmail"
                  type="email"
                  value={settings.companyEmail}
                  onChange={(e) => handleInputChange('companyEmail', e.target.value)}
                  placeholder="Enter company email"
                />
              </div>
            </CardContent>
          </Card>

          {/* Menu Settings */}
          <Card>
            <CardHeader>
              <CardTitle>Menu Settings</CardTitle>
              <CardDescription>
                Configure default menu preferences
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="defaultNotes">Default Menu Notes</Label>
                <Textarea
                  id="defaultNotes"
                  value={settings.defaultMenuNotes}
                  onChange={(e) => handleInputChange('defaultMenuNotes', e.target.value)}
                  placeholder="Enter default menu notes"
                />
                <p className="text-xs text-gray-500">
                  This text will be used as default notes for new daily menus
                </p>
              </div>

              <div className="p-4 bg-blue-50 rounded-lg">
                <h4 className="font-medium text-blue-900 mb-2">Current Settings Summary</h4>
                <div className="text-sm text-blue-800 space-y-1">
                  <p>• Orders close at {settings.orderCutoffTime}</p>
                  <p>• Reminders sent at {settings.orderReminderTime}</p>
                  <p>• Menu auto-locks at {settings.autoLockMenuTime}</p>
                  <p>• Max {settings.maxOrdersPerDay} order(s) per employee per day</p>
                  <p>• Weekend orders: {settings.enableWeekendOrders ? 'Enabled' : 'Disabled'}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Save Button (Mobile) */}
        <div className="lg:hidden">
          <Button onClick={handleSaveSettings} disabled={isSaving} className="w-full">
            {isSaving ? 'Saving...' : 'Save Settings'}
          </Button>
        </div>
      </div>
    </AdminLayout>
  )
}
